.navigator{float:right;margin:0.875em;font-weight:600;white-space:nowrap;overflow:hidden}.navigator__school{padding:0;margin:0;border:1px solid transparent;display:inline-block}.navigator__school:hover{border:1px solid #b6b6b6}.school__radio{clip:rect(1px, 1px, 1px, 1px);position:absolute !important;padding:0}.school__label{display:none;padding:0.25rem 0;color:#b6b6b6}.navigator__school:hover>.school__label{display:inline-block;padding:0.25rem 0.25rem}.school__radio:checked+.school__label{display:inline-block;color:#333}@media (min-width: 768px){.controls{margin-bottom:10em;min-height:17em}}



